.widgets {
   position:sticky;
   top: 80px;
   flex: .2;
   background-color: white;
   border-radius: 10px;
   border: 1px solid lightgray;
   height: fit-content;
   padding-bottom: 10px;

}

.widgets__header {

    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}

.widgets__header > h2 {
    font-size: 16px;
    font-weight: 40px;
}

.widgets__article {
    display: flex;
    padding: 14px;
    cursor: pointer;
}

.widgets__article:hover {
    background-color: whitesmoke;
}

.widgets__articleLeft {
    color: #0177b7;
}

.widgets__articleLeft > .MuiSvgIcon-root {
    font-size: 15px;
}

.widgets__articleRight {
    flex:1
}
.widgets__articleRight > h4 {
    font-size: 14px;
}
.widgets__articleRight > p {
    font-size: 12px;
    color: gray;
}